@import "../../assets/styles/app";

.app {
  position: relative;
  min-height: 100vh;
  background-image: url(../../assets/png/background.png);
  background-repeat: no-repeat;
  background-size: 1200px auto;
  background-position: top;
  box-sizing: border-box;
  padding: 260px 0 50px;

  //overflow: hidden;

  @include desktop {
    background-size: 1920px auto;
    padding: 500px 0 100px;
  }

  .baby {
    position: absolute;
    z-index: 1;
    transform: translateX(-50%);
    left: 50%;
    top: -10px;
    display: block;
    height: auto;
    width: 300px;

    @include desktop {
      width: 600px;
      top: -40px;
    }
  }

  .title {
    display: block;
    position: relative;
    z-index: 2;
    width: 300px;
    height: auto;
    margin: 0 auto;

    @include desktop {
      width: 1000px;
    }
  }

  .descriptionWrapper {
    background: linear-gradient(180deg, rgba(46, 96, 148, 0.00) 0%, #2E6094 46.5%, rgba(46, 96, 148, 0.00) 100%);
    backdrop-filter: blur(2px);
    padding: 25px 0;
    margin-top: -25px;

    @include desktop {
      padding: 50px 0;
      margin-top: -50px;
    }

    .description {
      color: #FFF;
      text-align: center;
      font-size: 30px;
      margin: 0 auto;

      @include desktop {
        font-size: 59px;
        width: 1200px;
      }
    }
  }

  .buttons {
    margin: 32px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      margin: 75px auto 0;
      width: 1200px;
      display: grid;
      grid-template-rows: repeat(2, 1fr);
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: 43px;
      grid-column-gap: 71px;
    }

    .button {
      @include button;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 285px;
      height: 65px;
      box-sizing: border-box;
      border-radius: 999px;
      border: 1px solid #B2ECF8;
      background: linear-gradient(180deg, #81A6DA 0%, #235282 100%);
      box-shadow: 10px 15px 7.3px 0px rgba(0, 0, 0, 0.25);
      transition: 0.3s;

      &:hover {
        @include desktop {
          box-shadow: 15px 17px 7.3px 0px rgba(0, 0, 0, 0.35);
        }
      }

      @include desktop {
        width: 569px;
        height: 171px;
      }

      &:not(:first-child) {
        margin-top: 12px;

        @include desktop {
          margin-top: 0;
        }
      }

      p {
        color: #FFF;
        font-size: 35px;

        @include desktop {
          font-size: 70px;
        }
      }

      svg {
        height: 25px;

        @include desktop {

          height: 71.514px;
        }
      }
    }
  }

  .address {
    padding: 0 16px;
    word-break: break-all;
    text-align: center;
    color: #FFF;
    font-size: 35px;
    margin-top: 24px;

    @include desktop {
      padding: 0;
      font-size: 70px;
      margin-top: 48px;
    }

  }

  .linkIcon {
    @include centerColumn;
    position: absolute;
    z-index: 2;
    transform: translateX(-50%);
    top: 20px;
    border-radius: 110px;
    border: 4px solid #84afda;
    background: linear-gradient(180deg, #81A6DA 0%, #235282 100%);
    box-shadow: 10px 15px 7.3px 0px rgba(0, 0, 0, 0.25);
    width: 75px;
    height: 75px;
    transition: 0.3s;

    @include desktop {
      width: 200px;
      height: 200px;
      top: 200px;
    }

    &:hover {
      @include desktop {
        box-shadow: 15px 17px 7.3px 0px rgba(0, 0, 0, 0.35);
      }
    }

    &_left {
      left: calc(50% - 100px);

      @include desktop {
        left: calc(50% - 500px);
      }
    }

    &_right {
      left: calc(50% + 100px);

      @include desktop {
        left: calc(50% + 500px);
      }
    }

    svg {
      height: 40px;

      @include desktop {
        height: 100px;
      }
    }

  }


}